import React, { useState } from 'react';

const ContactForm = () =>  {
  const [contactInfo, setContactInfo] = useState(0);

  const onChange = (event) => {
    console.log('event', event.target);
    setContactInfo({[event.target.id]: event.target.value })
  }

  const submitForm = (event) => {
    event.preventDefault();
    console.log('hey ho hey ho', contactInfo);
  }

  return <form onSubmit={submitForm}>
    <input id="fullName" type='text' placeholder='Full Name' onChange={onChange}></input>
    <input id="phone" type='phone' placeholder='8888888888' onChange={onChange}></input>
    <input id="email" type='email' placeholder='placeholder@email.com' onChange={onChange}></input>
    <input type='submit' name='submit' />
  </form>;
}

export default ContactForm;
